body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.outside-main {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: auto;
  background-color: #2F7FB1;

}



.homepage-header {
  font-size: 100px;
  margin-top: 20px;
}
.homepage-subline {
  font-size: 50px;
  font-style: italic;
}
.homepage-double {
  margin-top: 200px;
  display: grid;
  grid-template-columns: 1fr 1fr 2fr;
}
.homepage-links {
  font-size: 100px;
  text-align: left;
}
.homepage-link a {
  color: black;
}
.homepage-link a:hover {
  color: white;
  background-color: black;
  transition:  200ms ease-in-out;
}
.homepage-image {
  margin-right: 100px;
}
.homepage-image img {
  border-radius: 100%;
  max-width: 50%;
}

.outside-projects {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: auto;
  background-color: #2F7FB1;
}

.projects-header {
  margin-top: 30px;
  font-size: 120px;
}




.name {
  font-size: 90px;
  margin-top: 20px;
  margin-left: 20px;
}

.subline {
  font-size: 35px;
  margin-left: 5px;
  margin-bottom: 100px;
  font-style: italic
}

.section-left {
  text-align: right;
  font-size: 120px;
  text-decoration: underline;
  justify-items: right;
  width: 100%;
  margin-top: 10%;
  margin-right: 100px;
  background-color: white;
  margin-right: 100px;
}

.section-right {
  margin-top: 20%;
  width: 100%;
  font-size: x-large;
  text-align: right;
  font-size: 120px;
}

.section-full {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 100px;

}

.section-image {
  border-radius: 100%;
  min-width: 20%;
  max-width: 30%;
}





.projects {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: auto;
  background-color: lightslategray;
}

.project {
  position: relative;
  margin-top: 100px;
  text-align: center;
}

.project-caption {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: xxx-large;
}

.project-details {
  font-size: large;
}

.darken {
  transition: all 250ms ease-in-out;
  filter: brightness(20%);
  width: 60%;
  border-radius: 30px;
}

.darken:hover {
  filter: brightness(100%);
}

.darken:hover + .project-caption {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
}


.project-details {
  font-size: large;
  justify-self: first baseline
}