.halfs-outside {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: auto;
    background-color: white;
  }
  
  .left {
    width: 50%;
    height: 100%;
    float: left;
  }
  
  .right {
    width: 50%;
    height: 100%;
    float: right;
  }
  
  .top {
    width: 100%;
    height: 50%;
  }
  
  .bottom {
    width: 100%;
    height: 50%;
  }
  
  