
body {
  margin: 0;
  display: flex;
  place-items: center;
  min-width: 320px;
  min-height: 100vh;
  color: black;
  text-wrap: wrap;
  font-size: 10px;
  font-weight: bold;
}

.grid-monopoly {
    display: grid;
    height: 1000px;
    width: 1000px;
    grid-template: 2fr 1fr repeat(9, 2fr) 1fr 2fr / 2fr 1fr repeat(9, 2fr) 1fr 2fr;
    border: solid royalblue 10px;
    text-transform: uppercase;
    
  }
  
  .property-text-top {
    rotate: 180deg;
    margin-top: 45px;
  }
  
  .property-text-left {
    transform: rotate(90deg);
  }
  
  .property-text-left {
    transform: rotate(270deg);
    transform: translate(200px, 50px);
  }
  
  .rotate-left {
    rotate: 90deg;
    max-width: 90px;
    transform: translate(27px, -20px);
  }

  .rotate-left-full {
    rotate: 90deg;
    max-width: 90px;
  }

  .rotate-right {
    rotate: -90deg;
    max-width: 90px;
    transform: translate(-27px, -20px);
    align-self: center;

  }

  .rotate-right-full {
    rotate: -90deg;
    max-width: 90px;
  }
  
  
  .rotate-top {
    rotate: 180deg;
    margin-bottom: 10px;
  }
  
  .rotate-bottom {
    margin-top: 10px;
  }
  
  .rotate-top-left {
    rotate: 135deg;
  }
  
  .rotate-top-right {
    rotate: 225deg;
  }
  
  .rotate-bottom-left {
    rotate: 45deg;
  }
  
  .rotate-bottom-right {
    rotate: 315deg;
  }
  
